*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas {
  display: block;
  max-width: 100%;
  min-height: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

:root {
  --gold: hsl(24, 56%, 63%);
  --black: hsl(0, 0%, 8%);
  --gray: hsl(0, 0%, 27%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-shoulder: "Big Shoulders Display", cursive;
  --font-outfit: "Outfit", sans-serif;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.8rem;
}
@media (min-width: 75em) {
  body {
    font-size: 2.2rem;
  }
}

.heading-primary {
  font-size: 6rem;
  text-transform: uppercase;
  font-family: var(--font-shoulder);
}
.heading-primary span {
  display: block;
  line-height: 0.92;
}
@media (min-width: 48em) {
  .heading-primary {
    font-size: 7rem;
  }
}
@media (min-width: 75em) {
  .heading-primary {
    font-size: 9.6rem;
  }
}

.heading-secondary {
  font-size: 5rem;
  text-transform: uppercase;
  font-family: var(--font-shoulder);
}
.heading-secondary span {
  display: block;
  line-height: 0.92;
}
@media (min-width: 75em) {
  .heading-secondary {
    font-size: 6rem;
  }
}

.heading-tertiary {
  font-size: 2.4rem;
  text-transform: uppercase;
  font-family: var(--font-shoulder);
  color: var(--white);
}
.heading-tertiary span {
  display: block;
  line-height: 0.92;
}
@media (min-width: 75em) {
  .heading-tertiary {
    font-size: 3.2rem;
  }
}

.description {
  font-family: var(--font-outfit);
  color: var(--gray);
}
.description--white {
  color: var(--white);
}

.container {
  max-width: 34.3rem;
  margin: 0 auto;
}
@media (min-width: 36em) {
  .container {
    max-width: 42rem;
  }
}
@media (min-width: 48em) {
  .container {
    max-width: 68.9rem;
  }
}
@media (min-width: 64em) {
  .container {
    max-width: 96rem;
  }
}
@media (min-width: 75em) {
  .container {
    max-width: 110rem;
  }
}

.button {
  text-decoration: none;
  font-family: var(--font-shoulder);
  color: var(--white);
  background-color: var(--black);
  position: relative;
  width: 20.4rem;
  height: 7.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2.4rem 0;
  font-size: 2rem;
  letter-spacing: 3px;
  text-transform: uppercase;
  transition: all 0.4s;
}
.button::after {
  content: url("../assets/icon-arrow-right.svg");
  position: absolute;
  top: 0;
  left: 20.4rem;
  width: 5.6rem;
  height: 7.2rem;
  padding-top: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--gold);
  transition: all 0.4s;
}
.button:hover {
  background-color: var(--gold);
}
.button:hover::after {
  background-color: var(--black);
}

.hero {
  display: flex;
  flex-direction: column;
}
@media (min-width: 48em) {
  .hero {
    position: relative;
  }
}
@media (min-width: 64em) {
  .hero {
    flex-direction: row;
    background-color: #fff;
  }
}
.hero__content {
  margin-top: 3.2rem;
}
@media (min-width: 48em) {
  .hero__content {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    width: 44%;
    margin-top: 0;
    margin-right: 4rem;
  }
}
@media (min-width: 64em) {
  .hero__content {
    width: auto;
    inset: 0;
    transform: none;
    margin-right: 0;
  }
}
@media (min-width: 64em) {
  .hero__content .container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 100%;
  }
}
.hero__img {
  width: 100%;
}
@media (min-width: 48em) {
  .hero__img {
    width: 57%;
    object-fit: cover;
    height: 70rem;
  }
}
@media (min-width: 64em) {
  .hero__img {
    width: 100%;
    height: 80rem;
    z-index: 2;
  }
}
.hero__text {
  display: flex;
  flex-direction: column;
  row-gap: 3.2rem;
}
@media (min-width: 48em) {
  .hero__text {
    row-gap: 4.8rem;
  }
}
@media (min-width: 64em) {
  .hero__text {
    flex-direction: row;
    column-gap: 10rem;
    justify-content: center;
  }
}
@media (min-width: 75em) {
  .hero__text {
    column-gap: 29rem;
  }
}
.hero__text-content {
  display: flex;
  flex-direction: column;
  row-gap: 3.2rem;
}
@media (min-width: 48em) {
  .hero__text-content {
    row-gap: 4.8rem;
  }
}
@media (min-width: 64em) {
  .hero__text-content {
    width: 35rem;
  }
}
@media (min-width: 64em) {
  .hero__left {
    flex-grow: 1;
    background-color: var(--white);
    mix-blend-mode: difference;
    z-index: 1;
  }
}
@media (min-width: 64em) {
  .hero__right {
    flex-grow: 1;
  }
}

.gallery {
  padding-top: 12rem;
  padding-bottom: 12rem;
  display: grid;
  row-gap: 1.6rem;
}
@media (min-width: 48em) {
  .gallery {
    grid-template-areas: "grid-text-1 grid-image-1 grid-image-1" "grid-image-2 grid-image-2 grid-image-3" "grid-image-2 grid-image-2 grid-text-2";
    grid-template-columns: 1fr minmax(1rem, 15.5%) 1fr;
    gap: 1.1rem;
  }
}
@media (min-width: 48em) and (min-width: 75em) {
  .gallery {
    gap: 3rem;
  }
}
.gallery__image--1 {
  margin-bottom: 0.9rem;
}
@media (min-width: 48em) {
  .gallery__image--1 {
    grid-area: grid-image-1;
    margin-bottom: 0;
  }
}
@media (min-width: 48em) {
  .gallery__image--2 {
    grid-area: grid-image-2;
  }
}
@media (min-width: 48em) {
  .gallery__image--3 {
    grid-area: grid-image-3;
  }
  .gallery__image--3 img {
    width: 100%;
  }
}
.gallery__text {
  margin-bottom: 1.6rem;
}
@media (min-width: 48em) {
  .gallery__text--white {
    align-self: center;
  }
}
@media (min-width: 75em) {
  .gallery__text--white {
    max-width: 35rem;
  }
}
.gallery__text-content {
  display: flex;
  flex-direction: column;
  row-gap: 2.4rem;
}
@media (min-width: 75em) {
  .gallery__text-content {
    row-gap: 3.2rem;
  }
}
.gallery__text--black {
  background-color: var(--black);
  color: var(--white);
  margin-bottom: 0;
  padding: 4.8rem 2.4rem;
}
@media (min-width: 48em) {
  .gallery__text--black {
    padding: 8.5rem 2.8rem;
  }
}
@media (min-width: 75em) {
  .gallery__text--black {
    padding: 6.5rem 4.8rem;
  }
}

.footer {
  background-color: var(--black);
  padding: 4.8rem 0;
}
@media (min-width: 64em) {
  .footer {
    padding: 8rem 0;
  }
}
.footer__container {
  display: grid;
  row-gap: 3.8rem;
}
@media (min-width: 48em) {
  .footer__container {
    grid-template-columns: 11.5rem 1fr 10rem;
    column-gap: 6.5rem;
  }
}
@media (min-width: 75em) {
  .footer__container {
    column-gap: 14rem;
  }
}
@media (min-width: 48em) {
  .footer__description {
    margin-right: 5rem;
  }
}
@media (min-width: 75em) {
  .footer__description {
    font-size: 1.8rem;
  }
}
@media (min-width: 75em) {
  .footer__heading {
    width: 16rem;
  }
}

.sns {
  display: flex;
  column-gap: 2rem;
}
.sns__icon {
  fill: var(--white);
  cursor: pointer;
  transition: all 0.4s;
}
.sns__icon:hover {
  fill: var(--gold);
}